import { Button, Chip, Grid, Paper, Snackbar, Typography } from "@mui/material"
import styled from "styled-components";
import terraria from "../assets/terraria.png"
import ApiService from "../services/ec2InstanceAPI"
import { useEffect, useState } from "react"
import { serverName, serverUrl } from '../constants'
import { useAuth0 } from "@auth0/auth0-react";
import React from "react";
import Fade from '@mui/material/Fade'

import MuiAlert, { AlertProps } from '@mui/material/Alert';
import Loading from "./Loading";

const Img = styled('img')({
    display: 'block',
    maxWidth: '400px',
    border: '#0A1017 2px solid',
    borderRadius: '4px'
  });


interface InstanceState {
  status: string
  ip: string
}

const Alert = React.forwardRef<HTMLDivElement, AlertProps>(function Alert(
  props,
  ref,
) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

const HomeComponent = () => {

    const { stopInstance, startInstance, getInstanceState } = ApiService;
    const [instanceState, setInstanceState ] = useState<string | undefined>()
    const [instanceIp, setInstanceIp ] = useState<string | undefined>()
    const [openSnackbar, setOpenSnackbar] = useState(false)

    const [apiLoading, setApiLoading] = useState(false)

    const [ token, setToken ] = useState<string | undefined>()

    const { getAccessTokenSilently  } = useAuth0()

    useEffect(() => {

        setApiLoading(true)

        const loadInstanceState = async() : Promise<InstanceState>  => {
            return await getInstanceState()
        }

        const getAccessToken = async() : Promise<string> => {
          return await getAccessTokenSilently()
        }

        loadInstanceState()
        .then(result => {
            
          console.log(result)

            setInstanceState(result.status)
            setInstanceIp(result.ip)
        })
        .catch((e) => {
            console.error(e)
        })
        .finally(() => {
            setApiLoading(false)
        })
        
        getAccessToken()
        .then(result => {
          console.log(`token: ${result}`)
          setToken(result)
        })
        .catch((e) => {
          console.error(e)
        });
      

    }, [getAccessTokenSilently, getInstanceState])

    let intervalCheck: any;

    const checkStatus = ()  => {

      clearInterval(intervalCheck)
      // Check server status continuously for one minute, with 2 second intervals
      // Stops in about 18-40 seconds, starts in 6-20 seconds
      intervalCheck = setInterval(getServerStatus, 2*1000)
      setTimeout(() => clearInterval(intervalCheck), 60*1000)
    }

    const handleCloseSnackbar = () => {
      setOpenSnackbar(false)
    }

    const getServerStatus = async() => {
      var {status, ip} = await getInstanceState()

      setInstanceState(status)
      setInstanceIp(ip)
    }

    const toggleInstance = async () => {
      if(instanceState === "running") {
        await stopInstance(token ?? '')
        checkStatus()
      } else if(instanceState === "stopped") {
        await startInstance(token ?? '')
        checkStatus()
      } else {
        await getServerStatus()
      }

      setOpenSnackbar(true)

    }

    if(apiLoading) {
        return <Loading />
    }


    return (
        <Paper
        sx={{
          p: 2,
          margin: 'auto',
          maxWidth: 1100,
          marginTop: 2,
          flexGrow: 1,
          backgroundColor: (theme) =>
            theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
        }}
      >
        <Grid container spacing={2}>
          <Grid item>
              <Img alt="complex" src={terraria} />
          </Grid>
          <Grid item xs={12} sm container>
            <Grid item xs container direction="column" spacing={2}>
              <Grid item xs>
                <Typography gutterBottom variant="subtitle1" component="div">
                  {serverName}
                </Typography>
                <Typography variant="body2" gutterBottom>
                  <Chip label={instanceState} color={instanceState === 'running' ? "success" : instanceState === 'stopped' ? 'error' : 'warning'} />
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  Server IP: {instanceIp}
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  Url: {serverUrl}
                </Typography>
              </Grid>
              <Grid item>
                <Button 
                  variant="outlined" 
                  color={instanceState === 'running' ? "error" : instanceState === 'stopped' ? 'success' : 'warning'}
                  onClick={toggleInstance}
                  >
                    {instanceState === "running" ? "Stop" : instanceState === "stopped" ? "Start" : "Refresh"}
                </Button>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
        <Snackbar
          anchorOrigin={{ vertical: 'top', horizontal: 'center'}}
          open={openSnackbar}
          onClose={handleCloseSnackbar}
          TransitionComponent={Fade}
          message="I love turtles"
          key={Fade.name}
        />      

      </Paper>
      
    )
}

export default HomeComponent;
